<template>
  <div class="loginbox">
    <div class="header">
      <div class="k1">
        <a href="/" class="logo" style="float: left;"></a>
        <!-- <h1 class="hospitaltitle">EUSE</h1> -->
      </div>
    </div>
    <div class="main main_bg1 main_bg2">
      <div class="k1">
        <div class="login r">
          <ul class="hd" id="hd">
            <li class="on">EUSE</li>
          </ul>
          <ul class="bd" id="bd">
            <li style="display: block;">
              <input type="text" class="tex" placeholder="" v-model="username">
              <input type="password" class="tex tex2" placeholder="" v-model="password">
              <!-- <p style="font-size: 16px;color:#666;">
                  
                  <input
                    type="text"
                    class="tex tex2"
                    style="width: 100px;padding-left: 5px;"
                    placeholder=""
                  >
                  <span class="yzm">DFJ6</span>
                  <span class="changeimg"></span>
              </p>-->
              <div class="forgive">
                <label class="l">
                  <input type="checkbox" class="che" value="1" name="remember" id="remember">
                  
                </label>
                <a href class="a2 r">?</a>
              </div>
              <div class="clear"></div>
              <input class="but" type="button" value="     " @click="sendLogin">
              <a class="but register">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</a>
              <!-- <p class="p2">
                        <a href=""></a>
                        <a href=""></a>
              </p>-->
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="footer">
      <p>Copyright2019 EUSE@www.zhangqiangwo@qq.com </p>
    </div>
  </div>
</template>

<script>
import qs from "qs";
import service from "../../service";
import store from '../../store'

export default {
  components: {},
  data() {
    return {
      username: "",
      password: ""
    };
  },
  computed: {},
  watch: {},
  methods: {
    sendLogin() {
      if (this.username == "") {
        this.$message({
          type: "info",
          message: ""
        });
        return;
      }
      if (this.password == "") {
        this.$message({
          type: "info",
          message: ""
        });
        return;
      }
      let user = {
        username: this.username,
        password: this.password
      };
      service
        .login(qs.stringify(user))
        .then(res => {
          console.log(res);
          if (res.data.code == 200) {
            store.commit('setToken',res.data.token)
            localStorage.setItem('token', res.data.token)
            localStorage.setItem('user',JSON.stringify(res.data.user))
            store.commit('setUser',res.data.user)
            this.$router.replace("/");
            console.log(res);
          } else if (res.data.code == -1) {
            this.$message({
              type: "info",
              message: res.data.msg
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  // - this
  created() {},
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style scoped>
@import url(../../../static/css/loginStyle.css);
.loginbox {
  background: #fff !important;
}
.register {
  display: block;
  text-align: center;
  line-height: 42px;
  background: #fff !important;
  border: 1px solid #ccc;
  color: #606266 !important;
  cursor: pointer;
}
</style>


