import euNavi from "@/components/navi";
import service from "@/service";
import { getDate } from "@/untils/base.js";
import qs from "qs";
export default {
    components: {
        euNavi
    },
    data() {
        return {
            categorytitle: "",
            categoryBut: "",
            currentPage: 1,
            input: "",
            isreset: true,
            addoredit: true,
            page: {
                pagesizes: [12, 35, 50, 100],
                pagesize: 12,
                pagetotal: 100
            },
            formInline: {
                user: "",
                region: ""
            },
            dialogFormVisible: false,
            user: {
                username: "",
                password: "",
                isadmin: false
            },
            rules: {
                username: [
                    { required: true, message: "", trigger: "blur" },
                    { min: 3, max: 10, message: " 3  10 ", trigger: "blur" }
                ],
                password: [
                    { required: true, message: "", trigger: "blur" },
                    { min: 3, max: 18, message: " 3  18 ", trigger: "blur" }
                ]
            },
            multipleSelection: [],
            userlist: []
        };
    },
    computed: {
        nowuserData() {
            return (
                this.userlist.slice(
                    (this.currentPage - 1) * this.page.pagesize,
                    this.currentPage * this.page.pagesize
                ) || []
            );
        }
    },
    watch: {},
    methods: {
        addEdit(handle) {
            this.dialogFormVisible = true;
            this.addEditTitle(handle);
        },
        handleEdit(index, row, handle) {
            this.addEditTitle(handle);
            this.dialogFormVisible = true;
            row.password = '';
            this.user = row;
            console.log(index, row);
        },
        addEditTitle(handle) {
            if (handle == "add") {
                this.addoredit = true;
                this.resetForm();
                this.isreset = true;
                this.categorytitle = "";
                this.categoryBut = "";
            } else if (handle == "edit") {
                this.addoredit = false;
                this.categorytitle = "";
                this.categoryBut = "";
                this.isreset = false;
            }
        },
        formatter(row, column) {
            return row.address;
        },
        onSubmit() {
            console.log("submit!");
        },
        handleSizeChange(val) {
            this.page.pagesize = val
            console.log(` ${val} `);
        },
        handleCurrentChange(val) {
            this.currentPage = val
            console.log(`: ${val}`);
        },
        handleDelete(index, row, batch) {
            this.clearBatch();
            this.multipleSelection.push(row);
            /* this.$refs.multipleTable.toggleRowSelection(row); */
            console.log(this.multipleSelection);
            this.deletList(index, batch);
        },
        deletList(index, batch) {
            if (batch) {
                if (this.multipleSelection == 0) {
                    this.$message({
                        type: "info",
                        message: ""
                    });
                } else {
                    this.deletOpen(index, batch);
                }
            } else {
                this.deletOpen(index, batch);
            }
        },
        deletOpen(index, batch) {
            let users = this.multipleSelection;
            let checkAdmin = false
            users.forEach((v, k) => {
                if (v.username == 'admin') {
                    this.$message.error('');
                    checkAdmin = true;
                    return;
                }
            });
            if (checkAdmin) {
                return;
            }
            this.$confirm(", ?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning"
            })
                .then(() => {
                    let user = this.multipleSelection;

                    this.deleusers(index, user)

                })
                .catch(() => {
                    if (!batch) {
                        this.clearBatch();
                    }
                    this.$message({
                        type: "info",
                        message: ""
                    });
                });
        },
        deleusers(index, user) {

            service
                .deleteuser(user)
                .then(res => {
                    console.log(res);
                    let userlist = this.userlist
                    for (let i = 0; i < user.length; i++) {
                        let _id = user[i]._id
                        for (let j = 0; j < userlist.length; j++) {
                            if (_id == userlist[j]._id) {
                                this.userlist.splice(j, 1)
                            }
                        }
                    }
                    this.$message({
                        type: "success",
                        message: "!"
                    });
                })
                .catch(err => {
                    console.log(err);
                    this.$message.error('',err);
                });

        },
        clearBatch() {
            this.multipleSelection.splice(0, this.multipleSelection.length);
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
            console.log(this.multipleSelection);
        },
        submitForm(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    if (!this.user.isadmin) {
                        this.user.isadmin = false
                    }
                    let user = {
                        username: this.user.username,
                        password: this.user.password,
                        isadmin: this.user.isadmin
                    }
                    if (this.addoredit) {//
                        service.adduser(user).then((res) => {
                            if (res.data.code == 200) {
                                this.$message({
                                    type: "success",
                                    message:  res.data.msg
                                });
                                this.dialogFormVisible = false
                                this.getusers();
                            } else {
                                this.$message.error('');
                            }
                        }).catch((err) => {
                            console.log(err);
                            this.$message.error('',err);
                        })
                    } else {//
                        this.user.username = user.username;
                        this.user.password = user.password;
                        this.user.isadmin = user.isadmin
                        console.log(this.user);

                        service.edituser(qs.stringify(this.user)).then((res) => {
                            if (res.data.code == 200) {
                                this.$message({
                                    type: "success",
                                    message:  res.data.msg
                                });
                                this.dialogFormVisible = false
                                this.getusers();
                            } else {
                                this.$message.error('');
                            }
                        }).catch((err) => {
                            console.log(err);
                            this.$message.error('',err);
                        })
                    }

                } else {
                    console.log("error submit!!");
                    this.$message.error('',err);
                    return false;
                }
            });
        },
        getusers() {
            service
                .getusers()
                .then(res => {
                    if (res.data.code == 200) {
                        let datas = res.data.data.userlist;
                        let newdata = datas.map((v, k) => {
                            datas[k].creatat = getDate(v.creatat, false);
                        });
                        this.userlist = datas;
                        /* let pagesizes = [];
                        for (let i = 0; i < 3; i++) {
                          pagesizes.push(pagesize * i + 5);
                        } */
                        /* this.$set(this.page, "pagesizes", pagesizes); */
                        this.$set(this.page, "pagetotal", this.userlist.length);
                    }
                })
                .catch(err => {
                    this.$message.error('',err);
                    console.log(err);
                });
        },
        resetForm() {
            /* this.$refs[formName].resetFields(); */
            this.user = {
                username: "",
                password: ""
            };
        }
    },
    // - this
    created() {
        /* this.page.pagetotal = this.userlist.length */
    },
    // - DOM
    mounted() {
        this.getusers()
    },
    beforeCreate() { }, // - 
    beforeMount() { }, // - 
    beforeUpdate() { }, // - 
    updated() { }, // - 
    beforeDestroy() { }, // - 
    destroyed() { }, // - 
    activated() { } //keep-alive
};


